<template>
  <div>
    <header>
      <div class="container-fluid">
        <div class="row">
          <div class="col-xl-8 col-6 align-self-center">
            <div class="left-wrapper">
              <RouterLink to="/" class="logo">
                <img src="../assets/images/new-logo.svg" alt="Logo">
              </RouterLink>
              <div class="form search-form">
                <div class="form-group">
                  <input type="text" class="form-control" placeholder="What are you looking for?">
                  <input type="submit" class="btn-style bg no-icon shadow medium" value="Search">
                </div>
              </div>
              <div class="d-block d-xl-none">
                <div class="navbar-menu" :class="{'active' : isMenu}">
                  <ul class="menu">
                    <li><img src="../assets/images/icons/maeketplace-icon.svg" alt="icon"></li>
                    <li><a href="#">Become a Seller</a></li>
                    <li><a href="#">Sign in</a></li>
                  </ul>
                </div>
              </div>
            </div>

          </div>
          <div class="col-xl-4 col-6 text-end align-self-center">
            <div class="account-detail-wrapper">
              <ul class="account-detail">
                <li><a href="#" class="medium">Become a Seller</a></li>
                <li><a href="#" class="medium"><div class="six-dot"><img src="../assets/images/icons/six-dot-icon.svg" alt="icon"></div></a></li>
                <li><a href="#" class="btn-style bg no-icon shadow">Sign in</a></li>
              </ul>
              <div class="hamburger" @click="isMenu = !isMenu">
                <div class="hamburger-menu" :class="{'open' : isMenu}" data-menu="12">
                  <div class="icon"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
    <div class="bottom-header">
      <div class="container text-center">
        <div class="header-tags-slider d-block d-xl-none">
          <carousel :items="3" :autoplay="true" :autoplaySpeed="1000" :dots="false" :nav="false" :margin="30" :loop="true" :navText="['←','→']">
            
              <div v-for="slide in tagSlider" :key="slide"><a class="medium" :href="slide.link">{{ slide.name }}</a></div>
            
          </carousel>
        </div>
        <ul class="menu d-none d-xl-block">
          <li><img src="../assets/images/icons/home-icon.svg" alt="icon"></li>
          <li><a class="medium" href="#">Home</a></li>
          <li><a class="medium" href="#">Graphics & Design</a></li>
          <li><a class="medium" href="#">Digital Marketing</a></li>
          <li><a class="medium" href="#">Writing & Translation </a></li>
          <li><a class="medium" href="#">Video & Animation</a></li>
          <li><a class="medium" href="#">Music & Audio</a></li>
          <li><a class="medium" href="#">Programming & Tech</a></li>
          <li><a class="medium" href="#">Business</a></li>
          <li><a class="medium" href="#">Lifestyle</a></li>
          <li><a class="medium" href="#">Trending</a></li>
        </ul>
      </div>
    </div>
  </div>

</template>

<script>
import carousel from 'vue-owl-carousel'

export default {
  name: 'HeaderComponent',
  props: {
  },
  components: {
    carousel,
    
  },
  data(){
    return{
      isMenu: false,
      breakpoints: {
        // 700px and up
        300: {
          itemsToShow: 3,
          snapAlign: 'center',
        },
        // 1024 and up
        768: {
          itemsToShow: 4,
          snapAlign: 'start',
        },
        992: {
          itemsToShow: 5,
          snapAlign: 'start',
        },
      },
      tagSlider: [
        {
          name: 'Home',
          link: '#'
        },
        {
          name: 'Graphics & Design',
          link: '#'
        },
        {
          name: 'Digital Marketing',
          link: '#'
        },
        {
          name: 'Writing & Translation',
          link: '#'
        },
        {
          name: 'Video & Animation',
          link: '#'
        },
        {
          name: 'Music & Audio',
          link: '#'
        },
        {
          name: 'Programming & Tech',
          link: '#'
        },
        {
          name: 'Business',
          link: '#'
        },
        {
          name: 'Lifestyle',
          link: '#'
        },
        {
          name: 'Trending',
          link: '#'
        },
      ]
    }
  },
  mounted() {

  },
  methods: {}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



